.services{
    display: flex;
    margin-bottom: 2rem;
    margin-top: -3rem;
    justify-content: center;
    align-items: center;
}
.s-left{
    display: flex;
    flex-direction: column;
    margin-top: 9rem;
    position: relative;
}
.s-left>:nth-child(1){
    color: var(--black);
    font-size: 2.5rem;
    font-weight: bold;
}
.about-details
{
    font-size: 21px;
}


.s-left>:nth-child(2){
    color: var(--orange);
    font-size: 2.6rem;
    font-weight: bold;
}
.s-left>:nth-child(3){
    color: var(--black);
    font-size: 16px;
    margin-top: 1rem;
}
.s-button{
    width: 8rem;
    height: 2rem;
    margin-top: 1rem;
}
.third-box{
    margin-left: 180px;
}
@media screen and (max-width: 490px) {
    .third-box{
        margin-left: 0;
    }
  }
@media screen and (max-width: 490px) {
    .s-left>:nth-child(1){
        font-size: 2rem;
    }
    .s-left>:nth-child(2){
        font-size: 2rem;
    }
    .about-details
{
    font-size: 20px;
    text-align: justify;
}
/* .services{
    margin-top: -1rem;
} */
  }









.s-right{
    position: relative;
}

@media screen and (min-width: 490px) and (max-width: 991px) {
    .about-details
    {
        font-size: 30px;
        text-align: justify;;
       
    }
}