
.skill-name {
  display: flex;
  flex-direction: column;
  

}
.skill-hero
{
  width: 80%;
  display: flex; 
  justify-content: center;
  align-items: center; 
  margin-left: 13%;
 
}
.skill-name > :nth-child(1) {
  color: var(--black);
  font-size: 2.5rem;
  font-weight: bold;
}
.hero-icons-skill
{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  margin-left: 10%;
  gap: 30px;
}
.skill-name > :nth-child(2) {
  color: var(--orange);
  font-weight: bold;
  font-size: 2.5rem;
}
.skill-name > :nth-child(3) {
  font-weight: 100;
  font-size: 18px;
  text-align: justify;
  color: var(--black);
  margin-top: 13px;
}
.skill-button {
  width: 6rem;
}

.skill-right {
  position: relative;
}



.blue-circle {
  background: #194988;
  top: 0px;
  left: 50%;
  z-index: 1;
}
.yellow-circle {
  background: #f5c32c;
  left: 53%;
  top: 33%;
}
.main-circle > * {
  position: absolute;
}


.check
{
  
    font-weight: bold;
    font-size: 30px;
    
  

}
.skill-details-skill
{
   text-align: justify;
   font-size: 24px;
}
@media (max-width: 480px) {
  .main-circle{
    transform: scale(0.8);
  }
  .background-circle{
    width: 9rem;
    height: 9rem;
  }
}


@keyframes rotate {
  0%,
  100% {
    transform: rotate(0);
  }
  5% {
    transform: rotate(20deg);
  }
  10% {
    transform: rotate(30deg);
  }
  30% {
    transform: rotate(50deg);
  }
  50% {
    transform: rotate(70deg);
  }
  60% {
    transform: rotate(120deg);
  }
  70% {
    transform: rotate(160deg);
  }
  75% {
    transform: rotate(200deg);
  }
  80% {
    transform: rotate(260deg);
  }
  85% {
    transform: rotate(280deg);
  }
  90% {
    transform: rotate(300deg);
  }
  95% {
    transform: rotate(360deg);
  }
} 

@media screen and (max-width: 490px) {
 
  .skill-details-skill
{
   font-size: 20px;
}
.skill-hero
{
  width: 100%;
  display: flex; 
  justify-content: center;
  align-items: center; 
  margin-left: 0%;
  /* margin-left: 10%; */
 
}
.check
{
  
    font-weight: bold;
    font-size: 22px;
    
  

}
.hero-icons-skill
{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  /* margin-left: 18px; */
}
}
