#git
{
  width: 80%;
  margin-top: 150px;
}
.portfolio{
    display: flex;
    /* align-items: center; */
    /* justify-content: center; */
    flex-direction: column;
    /* height: 70vh; */
    /* margin-top: 80px; */
    /* padding-top: 100px; */
    /* padding-bottom: 150px; */
    margin-left: 14%;
    width: 90%;
  
}
.portfolio span:nth-of-type(1){
    color: var(--black);
    font-size: 1.8rem;
    font-weight: bold;
}
.portfolio span:nth-of-type(2){
    color: var(--orange);
    font-size: 2.8rem;
    font-weight: bold;
}

.portfolio img{
    width: 20rem;
    height: 230px;
    filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
    border-radius: 19px;
}
.grid-container {
    margin-top: 15px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
    align-items: center;
  }
  
  .grid-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    
    
  }
  .project-detail
  {
    /* text-align: center;   */
    text-align: justify;
    width: 400px;
    /* background-color: #09a672; */
    border-radius: 2%;
  }
  .project-detail :nth-child(1)
  {
    background-color: #09a672;
    border-radius: 2%;
  }

  .project-detail :nth-child(2)
  {
    background-color: #09a672;
    border-radius: 2%;
  }
  .project-detail :nth-child(3)
  {
    background-color: #09a672;
    border-radius: 2%;
  }
  
  
  .grid-item a {
    display: block;
  }
  .grid-item img {
    width: 400px;
    height: 250px;
  }
  
  .grid-item div {
    margin-top: 10px;
  }
  
