  /* For mobile devices */
@media only screen and (max-width: 500px) {
  .portfolio{
    margin-left: 0px;
  }
    .swiper-slide {
      width: 100%;
      margin-right: 0;
    }
    .grid-container {
        display: grid;
        width: 100%;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 40px;
        justify-content: center;
        align-items: center;
        margin-left: 0px;
      }
      .grid-item a {
        display: block;
        width: 70%;
      }
      .grid-item img {
        width: 400px;
        height: 250px;
      }
      .git-btn
      {
        margin-left: 20%;
        text-align: center;
      }
      #git{
        display: block;
      }
      .portfolio img{
        width: 14rem;
        height: 230px;
        filter: drop-shadow(-12px 15px 13px rgba(0, 0, 0, 0.25));
        border-radius: 19px;
        /* margin-left: -30px; */
    }
    .portfolio button{
        /* margin-left: px; */
    }
    .project-detail
    {
      
      /* text-align: center;   */
      margin-left: 30%;
      text-align: justify;
      width: 250px;
    }
  }
  
  @media only screen and (min-width: 500px) and (max-width: 667px) {
