.home{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    padding: 10px;
    margin-top: 150px;
    /* width: 200px; */
    /* margin-left: 8.5%; */
}
.h-left{
    display: flex;
    position: relative;
    flex-direction: column;
    gap: 1rem;
    margin-top: 2rem !important;
}
.name-details{
    font-size:"30px",
}

.h-name{
    display: flex;
    flex-direction: column;
    
}
.h-name>:nth-child(1){
    color: var(--black);
    font-size: 3rem;
    font-weight: bold;
    
}
.h-name>:nth-child(2){
    color: var(--teal);
    font-weight: bold;
    font-size: 2rem;
}
.h-name>:nth-child(4){
    font-weight: 100;
    font-size: 18px;
    text-align: justify;
    color: var(--black);
    margin-top: 13px;
}
.h-button{
    width: 6rem;
}
.h-icons{
    display: flex;
    gap: 0rem;
    margin-top: 0.4rem;
}
.h-icons>*{
    transform: scale(0.5);
    cursor: pointer;
}
 
#text {
        font-weight: bold;
      }




.h-right{
    position: relative;
    margin-top: 2rem !important;
    height: 340px !important;
}
.h-right>*{
    z-index: 1;
}
.h-right>:nth-child(1){
    position: absolute;
    top: 0px;
    animation: animate 8s linear infinite;
    width: 150px;
}
@keyframes animate{
    0%,100%{
        transform: translateY(0px);
    }
    25%{
        transform: translateX(50px);
    }
    50%{
        transform: translateY(60px);
    }
}
.h-box-one,
.h-box-two,
.h-box-three,
.h-box-four{
    width: 200px;
    height: 60px;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    background: white;
    border: 2px solid var(--orange);
    border-radius: 14px;
    box-shadow: 0px 20px 24px 3px rgba(251, 161, 40, 0.20);
}
.h-box-one{
    position: absolute;
    right: 60px;
    top: 140px;
}
.h-box-two{
    position: absolute;
    left: 160px;
    top: 70px;
}
.h-box-three{
    position: absolute;
    right: 60px;
    top: 300px;
}
.h-box-four{
    position: absolute;
    left: 130px;
    top: 230px;
}
