 @media screen and (max-width: 490px) {
  .h-name>:nth-child(1){
    font-size: 2rem;
}
.h-name>:nth-child(2){
    font-size: 2rem;
}
.name-details
      {
        text-align: justify;;
        font-size:"50px",
      } 
      .btn-home{
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .home{
        margin-top: 100px;
      }
}


.blur{
    position: absolute;
    border-radius: 50%;
    height: 14rem;
    width: 22rem;
    background: #edd0ff;
    z-index: -9;
    top: -8%;
    left: 56px;
    filter: blur(72px);
}






@media screen and (min-width: 490px) and (max-width: 991px) {
    .h-name>:nth-child(1){
        font-size: 2rem;
    }
    .h-name>:nth-child(2){
        font-size: 2.5rem;
    }
    .name-details
          {
            text-align: justify;
            font-size: 32px;
          } 

          .btn-home{
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 30px;
            font-weight: 300;
          }
          .image-pro
          {
            margin-top: 20px;
          }
      
     
}
